export class SdkUtils {
    static instance() {
        let r = Math.random();
        console.log(r);
        return r > 0.5 ? SdkUtils : null;
    }

    static openPop() {
        cc.assetManager.loadBundle('SDKBundle', (err: Error, bundle: cc.AssetManager.Bundle) => {
            if (err) {
                console.error(err)
                return;
            }
            bundle.load('Prefabs/SdkPop', cc.Prefab, (err: Error, prefab: cc.Prefab) => {
                if (err) {
                    console.error(err)
                    return;
                }
                let node = cc.instantiate(prefab);
                node.parent = cc.director.getScene().getChildByName('Canvas');
                node.setPosition(0, 0);
            });
        });
    }
}

cc['SdkUtils'] = SdkUtils.instance;